
































































































import { Vue, Component } from 'vue-property-decorator';

import { ServiceDialog } from '@/../helpers';

@Component({
    components: {},
})
export default class VuePageClass extends Vue {
    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// Init Data ////////////////////

    //////////////////// Init Select Option  ////////////////////

    //////////////////// Form Show ////////////////////
    private clickDialogSuccessConfirm(): void {
        let content: string = this.$i18n.Dialog_SaveSuccess;
        let title: string = this.$i18n.Status_Success;
        ServiceDialog.SuccessConfirm(this, content, title);
    }

    private clickDialogSuccess(content: string = this.$i18n.Dialog_SaveSuccess): void {
        let title: string = this.$i18n.Status_Success;
        ServiceDialog.Success(this, content, title);
    }

    private clickDialogError(): void {
        let content: string = this.$i18n.Error_Response_401;
        let title: string = this.$i18n.Status_Error;
        ServiceDialog.Error(this, content, title);
    }

    private clickDialogErrorConfirmOnly(): void {
        let content: string = this.$i18n.Error_Response_401;
        let title: string = this.$i18n.Status_Error;
        ServiceDialog.ErrorConfirmOnly(this, content, title);
    }

    private async clickDialogWarning(): Promise<void> {
        let content: string = this.$i18n.Dialog_ConfirmDelete;
        let title: string = this.$i18n.Dialog_Warning;
        await ServiceDialog.Warning(this, content, title)
            .then((result) => {
                if (result.value) this.clickDialogError();
            })
            .catch((e: any) => console.error(e));
    }

    private async clickDialogQuestion(): Promise<void> {
        let content: string = this.$i18n.Dialog_ConfirmDelete;
        let title: string = this.$i18n.Dialog_Question;
        await ServiceDialog.Question(this, content, title)
            .then(async (result) => {
                if (result.value) await this.clickDialogSuccess(this.$i18n.Dialog_DeleteSuccess);
            })
            .catch((e: any) => console.error(e));
    }

    private async clickDialogQuestionDoubleConfirm(): Promise<void> {
        let content: string = this.$i18n.Dialog_ConfirmDelete;
        let contentDouble: string = this.$i18n.Dialog_DoubleConfirmDelete;
        let title: string = this.$i18n.Dialog_Question;
        await ServiceDialog.Question(this, content, title)
            .then(async (result) => {
                if (result.value) {
                    await ServiceDialog.Question(this, contentDouble, title)
                        .then(async (result) => {
                            if (result.value) {
                                await this.clickDialogSuccess(this.$i18n.Dialog_DeleteSuccess);
                            }
                        })
                        .catch((e: any) => console.error(e));
                }
            })
            .catch((e: any) => console.error(e));
    }

    //////////////////// Form Set Data ////////////////////

    //////////////////// Modify API (save, delete, test)  ////////////////////

    //////////////////// Resolve text ////////////////////

    //////////////////// Modal ////////////////////
}
